<template>
    <div class="useCart_container">
        <head-top head-title="兑换会员" goBack="true"></head-top>
        <section class="useCart_body" v-if="userInfo">
            <header>
                兑换成功后会自动关联到当前账户:{{userInfo.username}}
            </header>
            <section class="useCart_input">
                <input type="text" placeholder="请输入10位卡号" maxlength="10" v-model="Numbers">
            </section>
            <section class="useCart_input">
                <input type="text" placeholder="请输入6位密码" maxlength="6" v-model="Password">
            </section>
            <div class="buttom_type" :class="{'buttom_type_color':state}" @click="Adduser">
                <div class="buttom_type_s">
                    兑换
                </div>
            </div>
        </section>
        <section class="useCart_tx">
            <header>----温馨提示----</header>
            <div class="useCart_tx_1">
                <p>新兑换的会员服务，权益以「会员说明」为准。</p>
                <p>月卡：<b>30次</b>减免配送费。</p>
                <p>季卡：<b>90次</b>减免配送费。</p>
                <p>年卡：<b>360</b>次减免配送费。</p>
                <p>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</p>
            </div>
        </section>
        <alert-tip v-show="showAlert" @closeTip="showAlert=false" :alertText="alertText"></alert-tip>
    </div>
</template>

<script>
import headTop from '../../../components/header/header'
import alertTip from '../../../components/common/alertTip'
import {mapState} from 'vuex'
import {vipCart} from '../../../service/getData'
export default {
    components: {
        headTop,
        alertTip
    },data () {
        return {
            Numbers:'',
            Password:'',
            showAlert:false,
            alertText:null,
        }
    },computed: {
        ...mapState([
            'userInfo'
        ]),
        state:function()
        {
            let states=this.Numbers!=''&&this.Numbers.length==10&&this.Password!=''&&this.Password.length==6;
            return states;
        }
    },methods: {
        async Adduser()
        {
            if(this.userInfo&&this.userInfo.user_id&&this.state)
            {
               let res= await vipCart(this.userInfo.user_id,this.Numbers,this.Password);
               if(res.message)
               {
                   this.showAlert=true;
                   this.alertText=res.message;
               }
            }
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.useCart_container{
    position: fixed;
    top: 0;
    left:0;
    bottom: 0;
    right: 0;
    background: #f1f1f1;
    z-index: 204;padding-top: 1.95rem;
}
.useCart_body{
    header{
            padding:0.5rem;
        @include sc(0.6rem,#cccc);
    }
    .useCart_input{
        border-bottom: 0.025rem solid #f1f1f1;
        input{
            @include wh(100%,2.3rem);
            padding:0.5rem;
            @include sc(0.6rem,#333);
        }
    }
}
.buttom_type{
    background: #ccc;
    margin:0.5rem;
    border-radius: 0.3rem;
    .buttom_type_s{
        padding:0.5rem;
        @include sc(0.6rem,#fff);
        text-align: center;
    }
}
.buttom_type_color{
    background: #4cd964;
}
.useCart_tx{
    margin-top: 1.5rem;
    header{
        @include sc(0.6rem,#ccc);
        font-weight: bold;
            text-align: center;
    }
    .useCart_tx_1{
        margin-left: 2rem;
        margin-top: 0.8rem;
        p{
            line-height: 1rem;
             @include sc(0.5rem,#ccc);
             b{
                 color:#ccc;
             }
        }
    }
}
</style>



